@font-face {
  font-family: "PT Sans";
  src: url("../fonts/../fonts/pTSans-Regular.woff2") format("woff2"), url("../fonts/../fonts/pTSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "PT Sans Narrow";
  src: url("../fonts/../fonts/pTSans-Narrow.woff2") format("woff2"), url("../fonts/../fonts/pTSans-Narrow.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/../fonts/pTSans-Bold.woff2") format("woff2"), url("../fonts/../fonts/pTSans-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal; }

html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  min-width: 320px;
  height: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #000000;
  background-color: #ffffff; }

img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover; }

a {
  text-decoration: none;
  color: inherit; }

.site-container {
  min-height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr;
  overflow: hidden; }

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer; }

.container {
  margin: 0 auto;
  max-width: 1059px;
  padding: 0 30px;
  width: 100%; }

.visually-hidden {
  position: absolute;
  clip-path: inset(100%);
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0); }

@media (max-width: 1023px) {
  .container {
    padding: 0 50px; } }

@media (max-width: 767px) {
  .container {
    padding: 0 15px; } }

.main-header__container {
  padding-top: 80px;
  padding-bottom: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.main-header__logo {
  display: flex;
  align-items: center;
  color: #000000;
  transition: all 0.3s ease-in-out;
  cursor: pointer; }
  .main-header__logo:hover {
    color: #62b9ea; }
  .main-header__logo:focus, .main-header__logo:active {
    color: #0a6ea6; }

.main-header__logo span {
  display: block;
  margin-left: 10px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-size: 64px;
  line-height: 83px; }

@media (max-width: 860px) {
  .main-header__container {
    flex-direction: column;
    padding-top: 30px;
    padding-bottom: 58px; }
  .main-header__logo {
    margin-bottom: 23px; } }

.user-navigation {
  display: flex;
  align-items: center; }

.user-navigation__links {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center; }

.user-navigation__item:not(:last-child) {
  margin-right: 19px; }

.user-navigation__link {
  font-size: 16px;
  line-height: 21px;
  color: #0078bb;
  transition: all 0.3s ease-in-out;
  text-decoration: underline; }
  .user-navigation__link:hover {
    color: #62b9ea;
    text-decoration: none; }
  .user-navigation__link:focus, .user-navigation__link:active {
    color: #0a6ea6; }

.user-navigation__button {
  margin-left: 20px;
  padding: 6px 29px;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff;
  background-color: #0078bb;
  transition: all 0.3s ease-in-out;
  border-radius: 4px; }
  .user-navigation__button:hover {
    background-color: #62b9ea; }
  .user-navigation__button:focus, .user-navigation__button:active {
    background-color: #0a6ea6; }
  .user-navigation__button:focus span,
  .user-navigation__button:active span {
    opacity: 0.6; }

.user-navigation__button span {
  color: currentColor;
  opacity: 1; }

@media (max-width: 860px) {
  .user-navigation {
    flex-direction: column; }
  .user-navigation__button {
    margin: 0; }
  .user-navigation__links {
    margin-bottom: 15px; } }

.main__container {
  display: grid;
  grid-template-columns: auto 300px;
  column-gap: 40px; }

.main__content {
  margin-left: -2px;
  max-width: 660px; }

.main__content h2 {
  margin: 0;
  margin-bottom: 11px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-size: 42px;
  line-height: 54px;
  font-weight: 400; }

.main__content p {
  margin: 0;
  margin-bottom: 40px; }
  .main__content p:last-of-type {
    margin-bottom: 32px; }

.main__content h3,
.main__images h3 {
  margin: 0;
  margin-bottom: 15px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 36px; }

.main__images h3 {
  margin-top: 10px; }

@media (max-width: 860px) {
  .main__container {
    padding-bottom: 109px;
    grid-template-columns: auto;
    row-gap: 60px; }
  .main__content {
    margin-left: 0;
    padding-left: 4px;
    max-width: 100%; }
  .main__content p {
    padding-right: 29px;
    margin-bottom: 47px; }
  .main__content p:last-of-type {
    margin-bottom: 48px; }
  .main__content h3,
  .main__images h3 {
    margin: 0 0 16px; }
  .main__images {
    padding-left: 5px; }
  .main__images h3 {
    margin-top: -1px; } }

.form__message {
  margin-bottom: 27px; }

.form__message textarea {
  padding: 9px 10px;
  width: 100%;
  height: 160px;
  resize: none;
  outline: none;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  transition: all 0.3s ease-in-out; }
  .form__message textarea:hover {
    border: 1px solid #a6a6a6; }
  .form__message textarea:focus, .form__message textarea:active {
    padding-top: 8px;
    border: 2px solid #62b9ea; }
  .form__message textarea::placeholder {
    color: #808080; }

.form__options {
  margin-bottom: 33px; }

.form__checkbox {
  margin-bottom: 29px;
  display: flex;
  align-items: center;
  position: relative; }

.form__checkbox input {
  position: absolute;
  top: 0;
  left: 0;
  appearance: none;
  width: 24px;
  height: 24px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  cursor: pointer; }
  .form__checkbox input:hover {
    border: 1px solid #a6a6a6; }
  .form__checkbox input:checked {
    width: 24px;
    height: 24px;
    background-image: url("../img/icons/checkbox-checked.svg");
    background-size: 17px 14px;
    background-position: center;
    background-repeat: no-repeat;
    border: 2px solid #62b9ea; }

.form__checkbox label {
  display: block;
  padding-top: 2px;
  padding-left: 35px; }

.form__contacts h3 {
  margin-bottom: 15px; }

.form__name {
  margin-bottom: 18px;
  display: flex;
  align-items: center; }

.form__name label {
  flex-shrink: 0;
  display: block;
  margin-right: 27px; }

.form__name input {
  padding: 7px 10px;
  height: 32px;
  width: 100%;
  border: 1px solid #d9d9d9;
  outline: none;
  border-radius: 4px;
  transition: all 0.3s ease-in-out; }
  .form__name input:hover {
    border: 1px solid #a6a6a6; }
  .form__name input:focus, .form__name input:active {
    border: 2px solid #62b9ea; }

.form__email {
  margin-bottom: 8px;
  display: flex;
  align-items: center; }

.form__email label {
  flex-shrink: 0;
  display: block;
  margin-right: 27px;
  padding-right: 30px; }

.form__email input {
  padding: 7px 10px;
  height: 32px;
  width: 100%;
  border: 1px solid #d9d9d9;
  outline: none;
  border-radius: 4px;
  transition: all 0.3s ease-in-out; }
  .form__email input:hover {
    border: 1px solid #a6a6a6; }
  .form__email input:focus, .form__email input:active {
    border: 2px solid #62b9ea; }

.form__contacts span {
  display: block;
  margin-left: 97px;
  margin-bottom: 61px;
  font-size: 14px;
  line-height: 18px;
  color: #808080; }

.form__button {
  margin-left: 242px;
  display: flex;
  align-items: center;
  padding: 10px 24px;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff;
  background-color: #0078bb;
  opacity: 1;
  transition: all 0.3s ease-in-out;
  border-radius: 4px; }
  .form__button:hover {
    background-color: #62b9ea; }
  .form__button:focus, .form__button:active {
    background-color: #0a6ea6; }
  .form__button:focus span,
  .form__button:active span {
    opacity: 0.6; }
  .form__button:focus svg,
  .form__button:active svg {
    opacity: 0.6; }

.form__button span {
  display: block;
  margin-left: 5px;
  font-size: 20px;
  line-height: 26px; }

@media (max-width: 1025px) {
  .form__button {
    margin: 0 auto;
    width: 98%;
    justify-content: center; }
  .form__message textarea {
    margin-left: 2px;
    width: 98%; }
  .form__contacts h3 {
    margin-bottom: 21px; }
  .form__name,
  .form__email {
    padding-left: 3px;
    align-items: flex-start;
    flex-direction: column; }
  .form__name {
    margin-bottom: 24px; }
  .form__name label,
  .form__email label {
    margin-bottom: 7px; }
  .form__name input,
  .form__email input {
    margin-left: -2px;
    width: 99%; }
  .form__contacts span {
    margin-left: 0;
    margin-bottom: 66px; } }

.photos-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-rows: 200px 400px 200px;
  row-gap: 16px; }

.photos-list__item {
  border-radius: 9px;
  overflow: hidden; }

@media (max-width: 860px) {
  .photos-list {
    margin-right: 3px;
    grid-template-rows: 224px 447px 224px; }
  .photos-list__item img {
    width: 100%;
    height: 100%; } }
